body{
	background-color: #ccc;
	color: #ffff80
}
ol li{
	list-style-type: none;
	padding-bottom: 20px
}
.head{
border-radius: 15px;
height: 50px;
margin-top: 50px;
 font-family: Red hat Display, sans-serif;
  box-shadow:  0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: row;
  align-items: center;
}
p{
	margin-left: 10px
}
.search{
	background: none;
	margin-left:700px ;
	border: none;
	border-bottom: 1px solid grey;
	outline: none;
}
.pic{
	margin-top: 130px;
	box-shadow:  0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
	width: 150px;
	height: 100px;
	margin-left: 50px;
	border-radius: 15px;
	background-image: url(a.jpg);
}
.name{
	margin-top: 10px;
	box-shadow:  0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
	width: 150px;
	height: 50px;
	margin-left: 50px;
	border-radius: 15px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.subjects_container{
	display: flex;
	flex-direction: row;
	margin-left: 20px;
}
.col{
display: flex;
	flex-direction: column;
	margin-left: 10px;
	margin-right: 150px
}
.row{
	display: flex;
	flex-direction: row;
}
.rowl{
	display: flex;
	flex-direction: row;
	margin-left: -850px;
	margin-top: 20px;
	font-size: 20px;
	font-weight: bolder;
}
.rowl >p{
	margin-right: 20px
}
h1{
	text-align: center;
}
footer{
	background-color: black;
	height: 400px;
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.cen{
	text-align: center;
}
.log_inp>input{
	border: none;
	background: none;
	/*border: 2px solid grey;*/
	display: flex;
	height:83.33px;
	outline: none;
	padding: 5px;
	box-shadow:  0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
	
}
.top_right_left{
	border-top-right-radius: 18px;
	border-top-left-radius:18px;
}
.bottom_right_left{
	border-bottom-right-radius: 18px;
	border-bottom-left-radius:18px;
}
.cent{
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}
.log_inp{
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	margin-top: 50px;
	/*box-shadow:  0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);*/
	width: fit-content;
	border-radius: 18px;
	height: 200px
}
.log{
	display: flex;
	align-items: center;
	justify-content: center;
	box-shadow:  0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
	border-radius: 18px;
	padding: 10px;
	width: 150px
}